import { useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import Todo from './Todo'; //Todo    import
import { List, Paper, Container, AppBar, Toolbar, Grid, Typography, Button } from '@mui/material'
import AddTodo from './AddTodo';
import axios from 'axios';
import { call, signout } from './service/ApiService';

// Container
//    ,        

//  props
// maxWidth :    (xs, sm, md, lg, xl, false)
// fixed : maxWidth    


function App() {
  //      
  // id, title, done
  const [items, setItem] = useState([])
  const [loading, setLoading] = useState(true);
  
  //     
  useEffect(() => {
    // 
    call("/todo", "GET")
      .then(result => {
        setItem(result.data)
        setLoading(false)
      });
  }, []);
  

  const add = (item) => {
    // DB     
    call("/todo", "POST", item)
    //  ,    state     
      .then(result => setItem(result.data));
  }

  //   deleteItem() 
  // useState(),    App.js  
  //  Todo App.js   
  // DB  delete from  where id = 0;...
  const deleteItem = (item) => {
    call("/todo", "DELETE", item)
      .then(result => setItem(result.data));
  }

  const editItem = (item) => {
    call("/todo", "PUT", item)
      .then(result => setItem(result.data));
  }


  // react key   ,       
  //    .
  const todoItems = items?.length > 0 && 
    // Paper 
    //      
    // elevation( )   
    //      
    <Paper style={{margin: 16}}>
       <List>  {/*       */}
        {items.map((item) => 
        <Todo item={item} key={item.id} deleteItem={deleteItem} editItem={editItem} />)}
      </List>
    </Paper>

  //  
  let navigationBar = (
    <AppBar position="static">
      <Toolbar>
        <Grid justifyContent="space-between" container sx={{ flexGrow : 1 }}>
          <Grid item>
            <Typography variant='h6'> </Typography>
          </Grid>
          <Grid item>
            <Button color='inherit' raised onClick={ signout }>
              
            </Button>
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
  )

  //      
  let todoListPage = (
    <div className="App">
      { navigationBar }
      <Container maxWidth="md">
        {/* AddTodo add  {add : function add(item) {~}} */}
        <AddTodo add={ add }/>
        <div className='TodoList'>
          { todoItems }
        </div>
      </Container>
    </div>
  );

  //      
  let loadingPage = <h1> ...</h1>
  let content = loadingPage;

  if(!loading) {
    content = todoListPage;
  }

  return (
    <div className="App">
      {content}
    </div>
  );
  
}

export default App;